<script>
export default {
  data: function () {
    return {
      nome: '',
      email: '',
      telefone: '',
      mensagem: '',
      modal: false,
      menuItens: [
        {
          label: 'Home',
          id: 'descricao'
        },
        {
          label: 'Sobre nós',
          id: 'descricao'
        },
        {
          label: 'Serviços',
          id: 'servicos'
        }
      ],
      vantagens: [
        {
          label: 'INSTALOU, PISOU!',
          icon: 'alarm',
          image: '0.jpg',
          descricao: 'Você instala e utiliza o ambiente no mesmo dia!'
        },
        {
          label: 'REFORMA SEM TRAUMA',
          icon: 'build',
          image: '1.jpg',
          descricao: 'Não gera entulho, pode ser instalado sobre outros pisos e fica pronto muito rápido.'
        },
        {
          label: 'VARIEDADE',
          icon: 'layers',
          image: '2.jpg',
          descricao: 'Muitas opções de padrões, texturas e dimensões para todos os gostos!'
        },
        {
          label: 'FÁCIL DE LIMPAR',
          icon: 'format_color_reset', // 'blur_off' // 'assistant'
          image: '3.jpg',
          descricao: 'Não precisa lavar, basta uma vassoura e um pano levemente úmido.'
        },
        {
          label: 'FÁCIL REDECORAR',
          icon: 'dashboard',
          image: '4.jpg',
          descricao: 'Você pode trocar seu piso antigo sem grandes obras ou quebra-quebra.'
        },
        {
          label: 'PARECE MADEIRA E É MADEIRA',
          icon: 'assignment_turned_in',
          image: '5.jpg',
          descricao: 'Produzido com madeira 100% de reflorestamento, poupando recursos naturais.'
        },
        {
          label: 'CONFORTO TÉRMICO',
          icon: 'hot_tub',
          image: '6.jpg',
          descricao: 'No verão ou no inverno, a sensação é de conforto.'
        },
        {
          label: 'HPP',
          icon: 'grain',
          image: '7.jpg',
          descricao: 'Fabricado com o HPP, o mais resistente substrato para pisos laminados do Brasil.'
        },
        {
          label: 'ANTIBACTERIANO',
          icon: 'local_hospital',
          image: '8.jpg',
          descricao: 'A proteção Bacterban inibe o crescimento e reprodução de fungos e bactérias na superfícies do seu piso.'
        },
        {
          label: 'REDUTOR DE RUÍDOS',
          icon: 'volume_off',
          image: '9.jpg',
          descricao: 'Reduz em até 60% a percepção de ruídos (toc-toc).'
        }
      ],
      center: 1,
      scrollY: 0
    }
  },
  computed: {
    vantagemAnterior () {
      return this.center > 0 ? this.vantagens[this.center - 1] : this.vantagens[this.vantagens.length - 1]
    },
    vantagemSeguinte () {
      return this.center < this.vantagens.length - 1 ? this.vantagens[this.center + 1] : this.vantagens[0]
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    updateCenter (val) {
      this.center += val
      if (this.center < 0) this.center = this.vantagens.length - 1
      if (this.center > this.vantagens.length - 1) this.center = 0
    },
    requestImage (id) {
      return require(`../../public/vantagens/${id}.jpg)`)
    },
    enviarEmail (ev) {
      ev.preventDefault()
    },
    handleScroll (event) {
      this.scrollY = window.scrollY
    },
    resetScroll () {
      window.scrollTo(0, 0)
    }
  }
}
</script>

